<script>
import index from "../../components/index.vue";
import FooterContent from "../../components/FooterContent.vue";
import { IssueService } from "@/services/issue.service";

export default {
components: { index, FooterContent, },
name: "issue",
data() {
return {
    token: localStorage.getItem("token"),
    userName: localStorage.getItem("userName"),
    dataIssue: [],
    dateRange: {
    from: null,
    to: null
    },
    search: {
    page: 1,
    size: 20,
    branchId:localStorage.getItem("branchId"), 
    },
    pagination: {
    total: 20
    },
};
},
created() {
this.fetchData();
},
methods: {
async fetchData() {
    try {
    const response = await IssueService.getList(this.token, this.search);
    if (response.status === 200) {
        this.dataIssue = response.data.listData;
        this.pagination.total = response.data.total
    }
    } catch (error) {
    console.log(error);
    }
},
CreateIssue(issueId){
    this.$router.push({ name: "CreateIssueDetail", params: { id: issueId } });
},
submitForm () {
    this.pagination.page = 0
    this.fetchData()
},

clearSearch () {
    this.search = {
    page: 1,
    size: 20,
    branchId:localStorage.getItem("branchId"), 
    }
    this.fetchData()
}
},

computed: {
useInUrlQueryPropList () {
    return this.prepareQueryParamsMixin({
    page: this.search.page
    })
}
},

};
</script>

